<template>
  <van-grid :column-num="5">
    <van-grid-item 
    v-for="item in channel" 
    :key="item.id"
    :icon="item.icon_url" 
    :text="item.name"
    @click="btn(item.id)"
    />
  </van-grid>
</template>

<script>
export default {
  name: 'MyGrid',
  props: ['channel'],
  methods: {
    btn(id) {
    //   this.$router.push({
    //     name: "Channel",
    //     query: { id },
    //   });
      this.$router.push("/channel?id="+id);
    }
  }
}
</script>

