<template>
  <div class="home">
    <van-search
      v-model="SearchVal"
      placeholder="请输入搜索关键词"
      disabled
      shape="round"
      @click="handleClickSearch"
    />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="blue">
      <van-swipe-item v-for="item in banner" :key="item.id">
        <img :src="item.image_url" alt="" width="100%" />
      </van-swipe-item>
    </van-swipe>
     <!-- 宫格 -->
     <MyGrid :channel="channel" />
     
     <BrandList :brandList="brandList"/>
     
      <NewProductList :goodsList="newGoodsList" title="周一周四·新品首发"/>
    
     <HotProduct :hotGoodsList="hotGoodsList" title="人气推荐"/>
     
     <TopicList :topicList="topicList" title="专题精选"/>
  
     
    <div class="category" v-for="item in categoryList" :key="item.id" >
     <CategoryList :goodsList="item.goodsList" :title="item.name"/>
    </div>

    <transition name="van-fade">
         <div class="poppup-shadow" v-show="$store.state.isShowPopupShadow"></div>
    </transition>
    <!-- <router-link :to="{path:'/home/searchPopup'}"> <p>这是首页</p></router-link> -->
    <!-- vue内置组件 -->
    <transition name="van-slide-right">
     <!-- SearchPopup占位 -->
      <router-view></router-view>
    </transition>
     <BackTop/>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import Vue from 'vue';
// import { Button } from 'vant';
// import 'vant/lib/button/style';
// Vue.use(Button);
// import "@/vantui"
// import axios from "axios";
// import request from '@/request/request'
import { GetHomeList } from "@/request/api";
import MyGrid from '@/components/MyGrid.vue';
import BrandList from "@/components/BrandList.vue";
import NewProductList from "@/components/NewProductList.vue"
import HotProduct from "@/components/HotProduct.vue" 
import TopicList from "@/components/TopicList.vue"
import CategoryList from "@/components/CategoryList.vue"
import BackTop from "@/components/BackTop.vue"

export default {
  name: "Home",
  data() {
    return {
      SearchVal: "夹克衫",
      banner: [],
      // 宫格
      channel: [],
      //品牌制造商
      brandList:[],
      //新品
      newGoodsList:[],
      //人气推荐
      hotGoodsList:[],
       // 专题精选
      topicList: [],
      //分类
      categoryList:[],

    };
  },
  watch:{
   '$route' (to,from){
    //  console.log(to)
    if(to.path=='/home'){
      this.$store.commit("changeIsShowPopupShadow",false);
    }
   }
  },
  components: {
    // HelloWorld
    MyGrid,
    BrandList,
    NewProductList,
    HotProduct,
    TopicList,
    CategoryList,
    BackTop 
  },
  created() {
    // request.get("/index/index")
    GetHomeList()
      .then((res) => {
        // console.log("success:");
        // console.log(res.data);
        // console.log(res.data.banner);
        this.banner = res.data.data.banner;
        this.channel=res.data.data.channel;
        this.brandList=res.data.data.brandList;
        this.newGoodsList=res.data.data.newGoodsList;
        this.hotGoodsList=res.data.data.hotGoodsList;
        this.topicList=res.data.data.topicList;
        this.categoryList=res.data.data.categoryList;
        // console.log(res);
        // console.log(this.channel);
      })
      .catch((err) => {
        // console.log("error");
        console.log(err);
      });
      //进入到此页面强制刷新
      // this.$router.go(0);
  },
  methods:{
    handleClickSearch(){
    this.$router.push('/home/SearchPopup');
    //修改vuex中的isShowPopupShadow的值为true
    this.$store.commit("changeIsShowPopupShadow",true)
    }
  }
};
</script>
<style lang="less" scoped>
.home{
height: 100vh;
width: 100%;
overflow: scroll;
padding-bottom: 0.5rem;//注意不能少
.category{
  background-color: #fff;

}
  
}
.slide-enter,
.slide-leave-to {
  // 过度之前的样式
  right: -100%;
}
.slide-enter-active,
.slide-leave-active {
  // 过度属性写在这里
  transition: right 0.3s;
}
.slide-enter-to,
.slide-leave {
  // 过度之后的样式
  right: 0;
}
.poppup-shadow{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color:rgba(0,0,0,.5);
  z-index: 25;
}
</style>
