import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Store from '../store'

const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'

  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    meta: {
      activeNum: 0,
      isShowTabbar:true,
      // keepalive:false,
    },
    children: [{
      path: '/home/searchPopup',
      name: 'SearchPopup',
      component: () => import(/* webpackChunkName: "about" */ '../views/SearchPopup.vue')
    }]

  },
  {
    path: '/topic',
    name: 'Topic',
    meta: {
      activeNum: 1,
      isShowTabbar:true
    },

    component: () => import(/* webpackChunkName: "about" */ '../views/Topic.vue')

  },
  {
    path: '/category',
    name: 'Category',
    component: () => import(/* webpackChunkName: "about" */ '../views/Category.vue'),
    meta: {
      activeNum: 2,
      isShowTabbar:true
    },
    
    children: [{
      path: '/category/searchPopup',
      name: 'SearchPopup',
      component: () => import(/* webpackChunkName: "about" */ '../views/SearchPopup.vue')
    }]


  },
  {
    path: '/cart',
    name: 'Cart',

    meta: {
      activeNum: 3,
      isShowTabbar:true
    },
    component: () => import(/* webpackChunkName: "about" */ '../views/Cart.vue')

  },
  {
    path: '/user',
    name: 'User',

    meta: {
      activeNum: 4,
      isShowTabbar:true
    },
    component: () => import(/* webpackChunkName: "about" */ '../views/User.vue')

  },
  {
    path: '/ProductDetail',
    name: 'ProductDetail',
    component: () => import(/* webpackChunkName: "about" */ '../views/ProductDetail.vue')

  },
  {
    path: '/channel',
    name: 'Channel',
    component: () => import(/* webpackChunkName: "about" */ '../views/Channel.vue')

  },
  {
    path: '/brand',
    name: 'Brand',
    component: () => import(/* webpackChunkName: "about" */ '../views/Brand.vue')

  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

//路由拦截，路由守卫

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 要去往的路由对象
  // console.log("to",to);
  // 从哪里出发的路由对象
  // console.log("from",from);
  let token = localStorage.getItem("token");
  // const  _this=this;
  // // console.log(_this);
  if (to.path == "/cart") {
    if (token) {
      next()
    } else {
      Vue.prototype.$toast.loading({
        message: '请先登录...',
        forbidClick: true,
      });
      setTimeout(() => {
        next("/user");
        //  // 修改vuex中的ifgoBack
        Store.commit("changeIfgoBack", true);
       
        // Vue.prototype.$router.push('/cart');
       
      }, 1000)
    }
    return
  }
  next();
})


export default router
