<template>
  <div class="catego">
    <h2>{{title}}</h2>
    <ul>
        <li v-for="item in goodsList" :key="item.id" @click="toProductDetail(item.id)">
             <img v-lazy="item.list_pic_url" alt="" style="display:block;" width="100%"/>
             <div class="title">{{item.name}}</div>
             <div class="price">{{item.retail_price | RMBformat}}</div>
         </li>
    </ul>
  </div>
</template>

<script>
export default {
data(){
    return{}
 },
 props:['goodsList','title'],
 methods:{
    toProductDetail(id){
        this.$router.push("/ProductDetail?id="+id);
        setTimeout(()=>{
          //强制刷新页面
          this.$router.go(0);
        },100)
      }
},
}
</script>

<style lang="less" scoped>
.catego{
   
   
    h2{
         font-size: 0.2rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
      
    }
ul {
  
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  li {
    width: 50%;
    text-align: center;
    .title {
      // 最多显示一行的文字，多余的内容会被省略
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .price {
      color: darkred;
    }
  }
}
}
</style>