import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShowPopupShadow:false,
    ifgoBack: false
  },
  mutations: {
    changeIsShowPopupShadow(state,payload){
      state.isShowPopupShadow=payload
    },
    //登录成功后返回到之前页面
    changeIfgoBack(state,payload) {
      state.ifgoBack = payload;
    }
  },
  actions: {
  },
  modules: {
  }
})
