<template>
  <div class="hot">
    <h2>{{title}}</h2>
    <ul>
        <li v-for="item in hotGoodsList" :key="item.id" @click="toProductDetail(item.id)">
            <div class="hotImg"><img v-lazy="item.list_pic_url" alt="" style="display:block;" width="100%"/></div>
            <div class="hotIntro">
                <p class="name">{{item.name}}</p>
                <p class="desc">{{item.goods_brief}}</p>
                <p class="price">{{item.retail_price | RMBformat}}</p>
            </div>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
data(){
   return{}
},
props:['hotGoodsList','title'],
methods:{
    toProductDetail(id){
        this.$router.push("/ProductDetail?id="+id);
        setTimeout(()=>{
          //强制刷新页面
          this.$router.go(0);
        },100)
      }
},
}


</script>

<style lang="less" scoped>
.hot{
    width:100%;
    background-color:#fff;
    margin-top: 0.2rem;
    h2{
        text-align: center;
        height: 0.6rem;
        line-height: 0.6rem;
        font-weight:bold;
        font-size: 0.15rem;
    }
    li{
        display: flex;
        widows: 100%;
        background-color: #fafafa;
        margin-bottom: 0.1rem;
        .hotImg{
           flex:3;
        }
        .hotIntro{
            flex:7;
            .name{
                font-size: 0.13rem;
                padding: 0.1rem 0;
            }
            .desc{
                font-size: 0.17rem;
                padding: 0.1rem 0;
            }
            .price{
                font-size: 0.16rem;
                padding-bottom: 0.2rem;
            }
        }
    }
}

</style>