import axios from "axios";

// 创建一个axios实例
const instance = axios.create({
    baseURL: "http://kumanxuan1.f3322.net:8001",
    timeout: 5000

})

//两种拦截器的区别
//封装axios拦截器，拦截的是请求，一般在需要在请求中添加请求头
//路由拦截器拦截的是业面，可以决定用户有没有权利访问某个页面

// request:axios的一个方法
instance.interceptors.request.use(config=>{
    //    config函数在发送请求之前执行,config包含本次请求的一些信息.
    //    如果未登录,就直接return.就不会再继续请求了
    // console.log(config); //本次请求的一些信息
    //  一定要有return返回值
    let token = localStorage.getItem("token");
    // console.log(token);
   if(token){
       config.headers["X-Nideshop-Token"]=token
    //    console.log(config);
   }
    return config;

},err=>{
    return Promise.reject(err);
})


// response:axios的一个方法
instance.interceptors.response.use(res => {
    //    res函数在响应之前执行,在执行then方法之前.
    //    res包含本次响应的一些信息
    // console.log(res);
    //  返回的res被then方法的res形参接收了,一定要有return 返回值
    return res; 
    // 也可以在这对数据进行处理
    // return res.data;
},err=>{
    return Promise.reject(err);
})

export default instance
