<template>
  <div class="new">
    <h2>{{title}}</h2>
    <ul>
      <li v-for="item in goodsList" :key="item.id"  @click="toProductDetail(item.id)">
        <img v-lazy="item.list_pic_url" alt="" width="100%" style="display: block;">
        <div class="title">{{item.name}}</div>
        <div class="price">{{item.retail_price | RMBformat}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ProductHome',
  props: ['goodsList','title'],
  methods: {
    // 跳转到商品详情页
    toProductDetail(id){
        this.$router.push("/ProductDetail?id="+id);
        setTimeout(()=>{
          //强制刷新页面
          this.$router.go(0);
        },100)
      }
  }
}
</script>

<style lang="less" scoped>
.new{
  margin-top: 0.2rem;
h2 {
  text-align: center;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.18rem;
  background-color: #fff;
  font-weight: normal;
}
ul {
  background-color: #fff;
  padding: .1rem 2%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  li {
    width: 49%;
    background-color: #fff;
    text-align: center;
    line-height: .3rem;
    margin-bottom: .1rem;
    .title {
      // 最多显示一行的文字，多余的内容会被省略
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .price {
      color: darkred;
    }
  }
}
}
</style>